import { Component, OnInit, ViewChild, ElementRef, ViewChildren, QueryList, Input, AfterViewInit } from '@angular/core';
import { Resource } from '../resource.model';
import { IResourceDetailsContext } from './resource-details.context';
import { NgbModalRef, NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { NotificationService } from 'src/app/services/notification.service';
import { NgModel } from '@angular/forms';

@Component({
  selector: 'app-resource-details',
  templateUrl: './resource-details.component.html',
  styleUrls: ['./resource-details.component.scss']
})
export class ResourceDetailsComponent<TModel extends Resource> implements AfterViewInit {
  @Input()
  context: IResourceDetailsContext<TModel>;

  protected modal: NgbModalRef;
  @ViewChild('content')
  public content: ElementRef<HTMLElement>;

  constructor(protected router: Router, protected notifier: NotificationService, protected modalService: NgbModal) {
  }

  ngAfterViewInit(): void {
    if (!this.context || !this.context.data || !this.context.properties)
    {
      this.notifier.warning('Something went wrong, Undefined context', 'Resource Details');
      this.done();
      return;
    }

    const options: NgbModalOptions = {
      size: 'md',
      backdrop: 'static',
    };
    this.modal = this.modalService.open(this.content, options);

    if (history.state.print)
    {
      setTimeout(() => { document.getElementById('printer').click(); }, 250);
    }
  }

  public done(): void {
    const url = this.context?.urls?.tableUrl || '/users';
    console.log(url);
    this.router.navigateByUrl(url).then(ok => {
      if (!ok)
      {
        return;
      }
      this.modal?.dismiss();
      this.modalService.dismissAll();
    });
  }
}
