import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute } from '@angular/router';
import { NotificationService } from 'src/app/services/notification.service';
import { environment } from 'src/environments/environment';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

export interface ResourceDeleteContext<T> {
  deleteUrl: string;
  data: T;
  title: string;
  description: string;
}

@Component({
  selector: 'app-resource-delete',
  templateUrl: './resource-delete.component.html',
  styleUrls: ['./resource-delete.component.scss']
})
export class ResourceDeleteComponent<TModel> implements AfterViewInit {
  @Input() context: ResourceDeleteContext<TModel>;

  private modal: NgbModalRef;
  @ViewChild('content') content: ElementRef;

  public uploading: boolean;

  constructor(
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private notifier: NotificationService) {
    this.uploading = false;
  }

  ngAfterViewInit(): void {
    const options: NgbModalOptions = {
      backdrop: 'static',
      size: 'sm'
    };

    this.modal = this.modalService.open(this.content, options);
    // console.log(this.modal);
    if (!this.modal || !history.state.resource)
    {
      this.ok();
    }
  }

  public delete(): void {
    this.uploading = true;
    this.http
      .delete<boolean>(environment.prepareUrl(this.context.deleteUrl, this.context.data))
      .subscribe(ok => {
        if (ok)
        {
          this.notifier.success('Resource deleted successfully');
          this.ok();
          this.uploading = false;
          return;
        }
        this.uploading = false;
        this.notifier.error('Somthing went wrong, Failed to delete the resource');
      }, err => this.uploading = false);
  }

  public ok(): void {
    if (this.modal)
    {
      this.modal.dismiss();
    } else
    {
      this.modalService.dismissAll();
    }
    this.router.navigate(['../../'], { relativeTo: this.route });
    // history.back();
  }
}
