import {
  Component, AfterViewInit, ComponentFactoryResolver, ViewChild,
  ViewContainerRef, Type, OnDestroy, AfterContentInit
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalOptions, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-resource-form-content',
  template: '<h1>Hello</h1>'
})
// tslint:disable-next-line: component-class-suffix
export abstract class ResourceFormContent implements AfterViewInit, AfterContentInit {
  title: string;
  message: string;
  formGroup: FormGroup;
  isEdit: boolean;

  public uploading: boolean;

  modal: NgbModalRef;

  abstract get postUrl(): string;
  abstract get putUrl(): string;
  abstract get parentUrl(): string;

  constructor(protected http: HttpClient, protected route: ActivatedRoute, protected router: Router, protected service: NgbModal) {
    this.message = 'Please fill the following form then click submit button';
    this.uploading = false;
  }

  ngAfterViewInit(): void {
    this.modal = this.openModal();
    this.initData();
  }

  ngAfterContentInit(): void {
  }

  initData(): void {
    const resource = history.state.resource;

    if (resource)
    {
      this.formGroup.patchValue(resource);
    }

    this.isEdit = this.route.snapshot.routeConfig.path.includes('edit');
    this.title = this.isEdit ? 'Edit Resource' : 'Create Resource';

    if (this.isEdit && !resource)
    {
      return this.ok();
    }
  }

  abstract openModal(): NgbModalRef;

  submit(): void {
    this.tryCreateResource();
    this.tryUpdateResource();
  }

  ok(): void {
    if (this.modal)
    {
      this.modal.dismiss();
    }
    else
    {
      this.service.dismissAll();
    }
    setTimeout(() => {
      this.router.navigateByUrl(this.parentUrl);
    }, 250);
    // history.back();
  }

  protected async tryCreateResource(): Promise<void> {
    if (this.isEdit)
    {
      return;
    }
    this.uploading = true;

    const apiUrl = environment.prepareUrl(this.postUrl, this.route.snapshot.params);

    return this.http.post(apiUrl, this.formGroup.value)
      .toPromise()
      .then(res => {
        console.log(res);
        if (res)
        {
          this.formGroup.reset();
          this.ok();
        }
      })
      .finally(() => this.uploading = false);
  }

  protected async tryUpdateResource(): Promise<void> {
    if (!this.isEdit)
    {
      return;
    }

    this.uploading = true;
    const apiUrl = environment.prepareUrl(this.putUrl, this.route.snapshot.params);

    return this.http.put(apiUrl, this.formGroup.value)
      .toPromise()
      .then(res => {
        console.log(res);
        if (res)
        {
          this.formGroup.reset();
          this.ok();
        }
      })
      .finally(() => this.uploading = false);
  }
}

@Component({
  selector: 'app-resource-form',
  templateUrl: './resource-form.component.html',
  styleUrls: ['./resource-form.component.scss']
})
export class ResourceFormComponent<TContent extends ResourceFormContent> implements AfterViewInit, OnDestroy {
  public static TYPES: Map<string, Type<any>> = new Map();

  modal: NgbModalRef;
  content: TContent;
  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;

  constructor(
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private resolver: ComponentFactoryResolver) {
  }

  ngAfterViewInit(): void {
    const options: NgbModalOptions = {
      size: 'md',
      backdrop: 'static',
    };

    const type = ResourceFormComponent.TYPES.get(this.route.snapshot.data.component);
    console.log(type);
    const factory = this.resolver.resolveComponentFactory<TContent>(type);
    const component = this.container.createComponent(factory);

    this.content = component.instance;
    this.modal = this.modalService.open(component.instance, options);
  }

  ngOnDestroy(): void {
    if (this.modal != null)
    {
      this.modal.dismiss();
      return;
    }
    this.modalService.dismissAll();
  }

  ok(): void {

  }
}
