import { Component, OnInit, Input } from '@angular/core';
import { NotificationService } from 'src/app/services/notification.service';
import { Router, ActivatedRoute, RouterEvent, NavigationStart, NavigationEnd, NavigationCancel, NavigationError } from '@angular/router';

import { ITableContext } from './table.context';
import { Resource } from '../resource.model';
import { LoaderService } from 'src/app/services/loader.service';
import { FormGroup, FormControl } from '@angular/forms';
import { SearchPipe } from '../search.pipe';
import { SlicePipe } from '@angular/common';

@Component({
  selector: 'app-table-view',
  templateUrl: './table-view.component.html',
  styleUrls: ['./table-view.component.scss']
})
export class TableViewComponent<TModel extends Resource> implements OnInit {
  @Input() context: ITableContext<TModel>;

  loading: boolean;

  pagination: { pageSize: number; page: number };
  find: FormGroup;

  constructor(
    private notifier: NotificationService, public router: Router, public route: ActivatedRoute,
    private search: SearchPipe, private slice: SlicePipe) {
    router.events.subscribe((routerEvent: RouterEvent) => {
      if (routerEvent instanceof NavigationStart)
      {
        this.loading = true;
      }

      if (routerEvent instanceof NavigationEnd || routerEvent instanceof NavigationCancel || routerEvent instanceof NavigationError)
      {
        this.loading = false;
      }
    });
  }

  ngOnInit(): void {
    this.pagination = {
      page: 1,
      pageSize: 10,
    };

    this.find = new FormGroup({
      key: new FormControl(),
    });

    if (!this.context)
    {
      this.notifier.error('Something went wrong, Table context is null');
    }

    if (!this.context.actions)
    {
      this.context.actions = [];
    }

    if (!this.context.behaviour || !this.context.behaviour.noDetails)
    {
      this.context.actions.push({
        // icon: 'fa fa-info-circle',
        icon: 'nc-icon nc-alert-circle-i',
        name: 'Details',
        type: 'url',
        url: this.context.urls.editUrl || ':id/details'
      });
    }

    if (!this.context.behaviour || !this.context.behaviour.noEdit)
    {
      this.context.actions.push({
        // icon: 'fa fa-edit',
        icon: 'nc-icon nc-tag-content',
        name: 'Edit',
        type: 'url',
        url: this.context.urls.editUrl || ':id/edit'
      });
    }

    if (!this.context.behaviour || !this.context.behaviour.noDelete)
    {
      this.context.actions.push({
        // icon: 'fa fa-trash-o',
        icon: 'nc-icon nc-simple-remove',
        name: 'Delete',
        type: 'url',
        url: this.context.urls.deleteUrl || ':id/delete'
      });
    }
  }

  public get resources(): TModel[] {
    const data = this.context.data;
    const filteredData = this.search.transform<TModel>(data, this.find.value.key);

    return filteredData;
    // const offset = (this.pagination?.page - 1) * this.pagination?.pageSize;

    // const paginatedData = this.slice.transform(filteredData, offset, offset + this.pagination?.pageSize);

    // return paginatedData;
  }

  resetPagination(): void {
    this.pagination.page = 1;
  }
}
